<template>
  <div class="d-flex flex-fill flex-column">
    <b-overlay :show="loading" rounded="sm">
      {{selectedPeriod}}
      <s-input v-model="selectedPeriod" :item="{ type:'month', validation:[], placeholder: 'Periode Laporan', api:'', formatter:momentFormat, ic:'w-100 mt-3' }" />
      <hr>
    </b-overlay>
    <p>Sebelum menghitung Gaji Tenaga Kerja, pastikan konfigurasi modul di bawah ini sudah terselesaikan :</p>
    <div v-if="!selectedPeriod" class="alert alert-custom alert-light-danger p-2 mb-3" role="alert">
      <div class="alert-icon">
        <span class="svg-icon svg-icon-danger svg-icon-xl">
          <svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
            <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
              <rect x="0" y="0" width="24" height="24" />
              <path d="M11.1669899,4.49941818 L2.82535718,19.5143571 C2.557144,19.9971408 2.7310878,20.6059441 3.21387153,20.8741573 C3.36242953,20.9566895 3.52957021,21 3.69951446,21 L21.2169432,21 C21.7692279,21 22.2169432,20.5522847 22.2169432,20 C22.2169432,19.8159952 22.1661743,19.6355579 22.070225,19.47855 L12.894429,4.4636111 C12.6064401,3.99235656 11.9909517,3.84379039 11.5196972,4.13177928 C11.3723594,4.22181902 11.2508468,4.34847583 11.1669899,4.49941818 Z" fill="#000000" opacity="0.3" />
              <rect fill="#000000" x="11" y="9" width="2" height="7" rx="1" />
              <rect fill="#000000" x="11" y="17" width="2" height="2" rx="1" />
            </g>
          </svg>
        </span>
      </div>
      <div class="alert-text">Periode penggajian belum dipilih!</div>
    </div>
    <div class="offcanvas-content pr-7 mr-n5 ps infinite flex-fill small-scroll">
      <b-overlay :show="isLoadingChecklist" rounded="sm">
        <div v-if="checklist" class="timeline timeline-5">
          <sequential-entrance fromBottom delay="100">
            <div v-for="(c,i) in checklist" :key="i+'-timeline-checklist'" class="timeline-items">
              <div class="timeline-item">
                <div class="timeline-media" :class="'bg-light-'+c.value">
                  <span class="svg-icon svg-icon-2x" :class="'svg-icon-'+c.value">
                    <svg v-if="c.value === 'success'" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <rect x="0" y="0" width="24" height="24" />
                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10" />
                        <path d="M16.7689447,7.81768175 C17.1457787,7.41393107 17.7785676,7.39211077 18.1823183,7.76894473 C18.5860689,8.1457787 18.6078892,8.77856757 18.2310553,9.18231825 L11.2310553,16.6823183 C10.8654446,17.0740439 10.2560456,17.107974 9.84920863,16.7592566 L6.34920863,13.7592566 C5.92988278,13.3998345 5.88132125,12.7685345 6.2407434,12.3492086 C6.60016555,11.9298828 7.23146553,11.8813212 7.65079137,12.2407434 L10.4229928,14.616916 L16.7689447,7.81768175 Z" fill="#000000" fill-rule="nonzero" />
                      </g>
                    </svg>
                    <svg v-else-if="c.value === 'danger'" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <rect x="0" y="0" width="24" height="24" />
                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10" />
                        <path d="M12.0355339,10.6213203 L14.863961,7.79289322 C15.2544853,7.40236893 15.8876503,7.40236893 16.2781746,7.79289322 C16.6686989,8.18341751 16.6686989,8.81658249 16.2781746,9.20710678 L13.4497475,12.0355339 L16.2781746,14.863961 C16.6686989,15.2544853 16.6686989,15.8876503 16.2781746,16.2781746 C15.8876503,16.6686989 15.2544853,16.6686989 14.863961,16.2781746 L12.0355339,13.4497475 L9.20710678,16.2781746 C8.81658249,16.6686989 8.18341751,16.6686989 7.79289322,16.2781746 C7.40236893,15.8876503 7.40236893,15.2544853 7.79289322,14.863961 L10.6213203,12.0355339 L7.79289322,9.20710678 C7.40236893,8.81658249 7.40236893,8.18341751 7.79289322,7.79289322 C8.18341751,7.40236893 8.81658249,7.40236893 9.20710678,7.79289322 L12.0355339,10.6213203 Z" fill="#000000" />
                      </g>
                    </svg>
                    <svg v-else xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="24px" height="24px" viewBox="0 0 24 24" version="1.1">
                      <g stroke="none" stroke-width="1" fill="none" fill-rule="evenodd">
                        <rect x="0" y="0" width="24" height="24" />
                        <circle fill="#000000" opacity="0.3" cx="12" cy="12" r="10" />
                        <rect fill="#000000" x="11" y="7" width="2" height="8" rx="1" />
                        <rect fill="#000000" x="11" y="16" width="2" height="2" rx="1" />
                      </g>
                    </svg>
                  </span>
                </div>
                <div @click="redirect(c.module_code ,c.object_data)" class="timeline-desc arrow-info-inside pt-0 pb-0 mb-3" :class="'timeline-desc-'+c.value">
                  <div class="p-2" :class="'bg-light-'+c.value" style="border-radius:8px">
                    <span class="text-dark-75 font-weight-bolder font-size-lg">{{ i+1 }}. {{ c.name }}</span>
                    <p class="mb-1">{{ c.description }}</p>
                    <template>
                      <div class="d-flex align-items-center">
                        <span class="font-size-sm font-weight-bolder text-hover-primary">Lihat Modul</span>
                        <i class="ri-arrow-right-line icon-arrow"></i>
                      </div>
                    </template>
                  </div>
                </div>
              </div>
            </div>
          </sequential-entrance>
        </div>
      </b-overlay>
    </div>
    <div>
      <hr>
      <span>Status Penyelesaian: <span class="font-weight-bolder text-uppercase" :class="status_overall? 'text-success': 'text-danger'">{{ status_overall | parse('status_overall') }}</span></span>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'todo-penggajian',
  props: {
    refreshKey: { type: Number, required: true }
  },
  data () {
    return {
      loading: false,
      isLoadingChecklist: false,
      status_overall: false,
      selectedPeriod: null,
      checklist: null,
      momentFormat: {
        stringify: (date) => {
          return date ? moment(date).format('MMMM YYYY') : ''
        },
        parse: (value) => {
          return value ? moment(value, 'MMMM YYYY').toDate() : null
        }
      }
    }
  },
  watch: {
    refreshKey: {
      deep: false,
      immediate: false,
      handler () {
        this.getThisMonth()
        this.getChecklist()
      }
    },
    selectedPeriod: {
      immediate: false,
      deep: true,
      handler (to, from) {
        if (to) {
          // this.$store.commit('set', ['lastSelectedPeriodPayroll', moment(moment(to, 'MMMM YYYY').toDate()).format('YYYY-MM-DD'), false])
          this.getChecklist()
        }
      }
    }
  },
  beforeMount () {
    this.getThisMonth()
  },
  methods: {
    sendAPI (method, api, param) {
      return this.$_api[method](api, param).then(res => {
        return res
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    async getThisMonth () {
      this.isLoadingPeriod = true
      console.log(this.$store.state.lastSelectedPeriodPayroll)
      const alreadySelect = this.$store.state.lastSelectedPeriodPayroll
      if (!alreadySelect) {
        let data = await this.sendAPI('list', 'payroll-periodes', { periode_month: moment(new Date()).set({ 'date': 1 }).format('YYYY-MM-DD') })
        if (data.data.length) {
          this.selectedPeriod = data.data[0].periode_month
          this.$store.commit('set', ['lastSelectedPeriodPayroll', data.data[0].periode_month, false])
        }
      } else this.selectedPeriod = moment(alreadySelect).format('MMMM YYYY')
      this.isLoadingPeriod = false
    },
    async getChecklist () {
      this.isLoadingChecklist = true
      let date = this.selectedPeriod
      if (date) date = moment(moment(date, 'MMMM YYYY').toDate()).format('YYYY-MM-DD')
      else date = null
      let data = await this.sendAPI('get', 'custom/payroll-periode-calculation/checklist', { periode_month: date })
      this.checklist = data.data
      this.status_overall = data.status_overall
      //   this.$set(this.mainData, 'checklist', data.data)
      //   this.$set(this.mainData, 'status_overall', data.status_overall)
      //   this.getDetailPayroll()
      this.isLoadingChecklist = false
    },
    getRouterName (e) {
      if (e === 'payroll_periodes') return 'periode-penggajian'
      else if (e === 'employee_on_periodes') return 'periode-penggajian'
      else if (e === 'presence_on_periodes') return 'rekap-presensi'
      else if (e === 'koperasi_simpan_pinjam') return 'simpan-pinjam'
      else if (e === 'bpjs_kes_bills') return 'iuran-BPJS-kesehatan'
      else if (e === 'bpjs_tk_bills') return 'iuran-BPJS-tenaga-kerja'
      else if (e === 'tunjangan_kehadiran') return 'tunjangan-kehadiran'
      else if (e === 'tunjangan_transports') return 'tunjangan-transports'
      else if (e === 'tunjangan_makan') return 'tunjangan-makan'
      else if (e === 'tunjangan_beasiswa') return 'tunjangan-beasiswa'
      else if (e === 'tunjangan_lembur') return 'tunjangan-lembur'
      else if (e === 'pinjaman_bank') return 'pinjaman-bank'
      else if (e === 'potongan_sanksi') return 'potongan-sanksi'
      else return 'perusahaan'
    },
    redirect (module, data) {
      const routeName = this.getRouterName(module)
      if (data && data.id) this.$router.push({ name: routeName, query: { view: 'detail', id: data.id } }).catch((err) => { return err })
      else this.$router.push({ name: routeName, query: { view: 'list' } }).catch((err) => { return err })
    }
  }
}
</script>

<style scoped>
  .arrow-info-inside {
    cursor: pointer;
  }
  .arrow-info-inside .icon-arrow {
    transition: all 0.5s ease;
    padding-left: 0.25rem;
  }
  .arrow-info-inside:hover .icon-arrow {
    padding-left: 0.8rem;
    color: var(--primary);
  }
  .timeline-badge.custom-badge {
    width: 15px !important;
    height: 15px !important;
  }
  .timeline-bar.custom-bar {
    left: 6.5px !important;
    width: 2px !important;
  }
  .scrolling-view {
    padding-right: 1rem;
    max-height: 80vh;
    overflow-y: auto;
  }
  .infinite {
    height: 70vh;
    position: relative;
    overflow: auto;
  }
  .switch.switch-icon.icon-lock input:checked ~ span:after {
    font-family: 'remixicon' !important;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    content: '\EECB';
    font-size: 12px;
  }
</style>



